.ofc-project {
  width: 95%;
  padding: 2rem;
}

.projects-grid {
  display: flex;
  flex-direction: column; /* Stack the items vertically */
  gap: 1.5rem;
  margin-top: 0.3rem;
  animation: fadeIn 1s ease-in-out;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.project-card {
  background: #e6e6e6;
  padding: 1.5rem; /* Adjust padding for better spacing */
  border-radius: 1rem;
  box-shadow: 0.3rem 0.3rem 0.6rem #c8d0e7, -0.2rem -0.2rem 0.5rem #ffffff;
  transition: all 0.3s ease-in-out;
  opacity: 0;
  animation: fadeIn 0.6s forwards;
  width: 97%; /* Full width of the container */
  margin: 0 auto; /* Center the cards horizontally */
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0.6rem 0.6rem 1.2rem #c8d0e7, -0.4rem -0.4rem 0.8rem #ffffff;
}

.project-title {
  font-size: 1.5rem; /* Adjusted font size for better readability */
  font-weight: bold;
  margin-bottom: 1rem;
  color: #333;
}

.project-card p {
  font-size: 1rem; /* Slightly smaller font for project description */
  color: #555;
}

.project-skills {
  font-size: 1rem; /* Adjusted skill font size */
  color: #333;
  margin-top: 1rem;
  display: flex;
  flex-wrap: wrap;
}

.project-skills strong {
  font-weight: bold;
  margin-right: 0.5rem;
}

.skill-icon {
  display: flex;
  align-items: center;
  margin-right: 1rem;
  font-size: 1rem; /* Adjusted skill name font size */
}

.skill-icon svg {
  margin-right: 0.5rem;
  font-size: 1.5rem; /* Increased icon size */
}

.project-date {
  font-size: 0.9rem; /* Slightly smaller font for date */
  color: #888;
  margin-top: 1rem;
}

/* Responsive Design for Mobile */
@media (max-width: 768px) {
  .project-card {
    padding: 1rem; /* Less padding on mobile */
  }

  .project-title {
    font-size: 1.25rem; /* Smaller title font */
  }

  .project-card p {
    font-size: 0.9rem; /* Smaller project description font */
  }

  .project-skills {
    font-size: 0.9rem; /* Slightly smaller skills font on mobile */
  }
}

/* Further adjust for very small screens (phones) */
@media (max-width: 480px) {
  .projects-grid {
    flex-direction: column; /* Stack the cards vertically on smaller screens */
  }

  .project-card {
    padding: 1rem; /* Smaller padding on very small screens */
  }

  .project-title {
    font-size: 1.1rem; /* Even smaller title font on smaller screens */
  }
}
