.skills-container {
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 20px;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1200px; /* This will make the container responsive and fit within the screen */
  }
  
  .skill-category {
    width: 100%;
    background: linear-gradient(145deg, #e6e6e6, #ffffff);
    border-radius: 12px;
    box-shadow: 5px 5px 15px #bebebe, -5px -5px 15px #ffffff;
    padding: 16px;
    text-align: center;
  }
  
  .skill-category h2 {
    font-size: 24px;
    margin-bottom: 15px;
    color: #333;
  }
  
  .skills-list {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    justify-content: center;
  }
  
  .skill-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: linear-gradient(145deg, #e6e6e6, #ffffff);
    border-radius: 10px;
    padding: 15px;
    width: 90px; /* Make this width smaller to ensure that everything fits */
    height: 90px; /* Same for height */
    box-shadow: 5px 5px 15px #bebebe, -5px -5px 15px #ffffff;
    transition: transform 0.3s ease-in-out;
  }
  
  .skill-item:hover {
    transform: scale(1.1);
  }
  
  .skill-item svg {
    font-size: 30px; /* Set a larger size to make the icons more visible */
    color: #333;
  }
  
  .skill-item span {
    margin-top: 3px;
    font-size: 12px;
    font-weight: bold;
    color: #333;
  }
  